.design1 {
  margin: 0;
}
.create1 {
  margin: 0;
  color: var(--color-skyblue);
}
.inspire1 {
  margin: 0;
  color: var(--color-mediumturquoise);
}
.design-create-inspire-container1 {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-53xl);
}
.lorem-ipsum-dolor1 {
  align-self: stretch;
  position: relative;
  color: var(--color-white);
}
.contact-us {
  position: relative;
  line-height: 18px;
}
.primary-button1,
.text {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.primary-button1 {
  text-decoration: none;
  border-radius: var(--br-17xl);
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-5xl) var(--padding-29xl);
  color: var(--color-gray);
}
.text {
  width: 359px;
  flex-direction: column;
  gap: var(--gap-5xl);
}
.frame-icon {
  position: relative;
  border-radius: var(--br-21xl);
  width: 172px;
  height: 192px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-container {
  flex: 1;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.div,
.image-gallery {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.image-gallery {
  width: 742px;
  flex-shrink: 0;
  justify-content: flex-start;
  gap: var(--gap-lg);
}
.div {
  position: relative;
  background-color: var(--color-darkslategray);
  width: 100%;
  overflow: hidden;
  justify-content: space-between;
  padding: var(--padding-48xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-pink);
  font-family: var(--font-poppins);
}
